<template>
  <div class="login-box">
    <div class="login-box-login">
      <p class="login-box-login-top">登录</p>
      <input type="text" class="login-box-inp-user" v-model="form.username" placeholder="请输入用户名" />
      <input type="password"  v-model="form.password" class="login-box-inp-pwd" placeholder="请输入密码" />
      <div class="login-box-inp-div">
        <button class="login-box-inp-btn" @click="login">登录</button>
      </div>
    </div>
  </div>
</template>
<script>
import {mapActions} from 'vuex'
import { reqUserLogin } from "../../util/request";
import { alertSuccess, alertWarning } from "../../util/alert";
export default {
  components: {},
  data() {
    return {
      form: {
        username: "",
        password: ""
      }
    };
  },
  methods: {
    ...mapActions({
        changeUserInfoAction:'changeUserInfoAction'
    }),
    login() {
      reqUserLogin(this.form).then(res => {
        if (res.data.code === 200) {
          alertSuccess(res.data.msg);
          this.changeUserInfoAction(res.data.list)
          this.$router.push("/index/home");
        }
        
      });
    }
  },
  mounted() {}
};
</script>
<style scoped>
.login-box {
  width: 100%;
  height: 100vh;
  background: url(../../assets/img/logo1.jpg);
  overflow: hidden;
}
.login-box-login {
  width: 499px;
  height: 299px;
  background-color: #ffffff;
  margin: 126px auto;
  border-radius: 10px;
  text-align: center;
}
.login-box-login-top {
  font-size: 22px;
  width: 100%;
  padding-top: 35px;
  margin-bottom: 23px;
}
.login-box-inp-user {
  width: 310px;
  height: 40px;
  padding-left: 10px;
  border: 1px solid #dcdfe6;
  margin-bottom: 22px;
  border-radius: 5px;
}
.login-box-inp-pwd {
  width: 310px;
  height: 40px;
  padding-left: 10px;
  border: 1px solid #dcdfe6;
  border-radius: 5px;
  margin-bottom: 22px;
}
.login-box-inp-btn {
  border: none;
  background-color: #409eff;
  width: 320px;
  height: 40px;
  border-radius: 5px;
  color: #ffffff;
  font-size: 12px;
}
.login-box-inp-pwd::placeholder {
  color: #dcdfe6;
}
.login-box-inp-user::placeholder {
  color: #dcdfe6;
}
.login-box-inp-btn {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
}

.login-box-inp-btn:hover {
  background-color: #2873d6;
  color: white;
}
</style>


